.infoDetails{
    // margin-top: 10px;
    // width: 900px;
    height: max-content;
    border: 1px solid rgba(0, 0, 0, 0.344);
    border-radius: '5px';
    padding-bottom: 20px;
    margin-bottom: 20px;
    .Details{
        margin: 0px 50px;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 40px;
        row-gap: 15px; 
    }
}
.infoAddress{
    margin-top: 10px;
    width: 1100px;
    height: 220px;
    border: 1px solid rgba(0, 0, 0, 0.344);
    .Address{
        display: grid;
        margin-left: 50px;
        grid-template-columns:300px 300px 300px;
        column-gap: 35px;
        row-gap: 15px; 
    
    }
}